@media (min-width: 768px) {
  .advantages__item {
    position: absolute;
    top: 50%;
    display: flex;
    justify-content: start;
    align-items: stretch;
    width: 50%;
    transform: rotate(40deg);
    transform-origin: 100% 50%;
    translate: 0 -50%;
  }
}
.advantages__icon-wrapper {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  background-color: rgba(149, 100, 170, 0.1);
  border-radius: 50%;
}
@media (min-width: 768px) {
  .advantages__icon-wrapper {
    width: 50px;
    height: 50px;
  }
}
.advantages__icon-wrapper svg {
  transition: fill 0.3s ease;
  fill: #9564aa;
}
@media (min-width: 768px) {
  .advantages__icon-wrapper svg {
    width: 24px;
    height: 24px;
  }
}
.advantages__item-grid {
  position: relative;
  display: flex;
  gap: 10px;
}
@media (min-width: 768px) {
  .advantages__item-grid {
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
  }
}
@media (hover: hover) {
  .advantages__item-grid:hover:not(.focus-visible) svg {
    fill: #ffcf2d;
  }
  .advantages__item-grid:hover:not(.focus-visible) .advantages__text {
    overflow: visible;
    background-color: #ffffff;
    -webkit-line-clamp: unset;
    -webkit-box-orient: unset;
    /* stylelint-disable-next-line */
    box-orient: unset;
    /* stylelint-disable-next-line */
    display: unset;
    /* stylelint-disable-next-line */
    display: unset;
  }
}
.advantages__text {
  align-self: center;
}
@media (min-width: 768px) {
  .advantages__text {
    position: absolute;
    top: 54px;
    left: 50%;
    width: 120px;
    overflow: hidden;
    text-align: center;
    transform: translate(-50%, 0%);
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    /* stylelint-disable-next-line */
    box-orient: vertical;
    /* stylelint-disable-next-line */
    display: -moz-box;
    /* stylelint-disable-next-line */
    display: -webkit-box;
  }
}

